/* src/Auth.css - This file contains your auth page styles */

.auth-container {
    height: 100vh;
    width: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-image 1s ease-in-out;
    animation: fade 1s ease-in-out;
}

@keyframes fade {
    from { opacity: 0.4; }
    to { opacity: 1; }
}

.auth-box {
    background: rgba(0, 0, 0, 0.25); /* transparent dark background */
    padding: 2rem 3rem;
    border-radius: 15px;
    box-shadow:
        0 0 10px rgba(0, 255, 255, 0.8),
        0 0 20px rgba(0, 255, 255, 0.6),
        0 0 30px rgba(0, 255, 255, 0.4);
    text-align: center;
    min-width: 300px;
    border: 1px solid rgba(0, 255, 255, 0.6);
    backdrop-filter: blur(8px); /* blur the background behind box */
    color: #fff;
}

.auth-box h2 {
    margin-bottom: 1rem;
    color: #00ffff;
    text-shadow: 0 0 5px #0ff, 0 0 10px #0ff;
}

.auth-box form input {
    display: block;
    margin: 0.5rem auto;
    width: 100%;
    padding: 0.6rem;
    font-size: 1rem;
    border-radius: 8px;
    border: 1px solid #aaa;
}

.auth-box button {
    margin-top: 1rem;
    padding: 0.6rem 2rem;
    font-size: 1rem;
    border: none;
    background-color: #0b7285;
    color: white;
    border-radius: 8px;
    cursor: pointer;
}

.auth-box p, .auth-box a {
    color: #0ff;
    text-shadow: 0 0 5px #0ff;
}
.auth-box a {
    color: #0b7285;
    font-weight: bold;
    text-decoration: none;
}