/* General Body/HTML defaults */
body {
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
    background-color: #1a1a2e; /* Dark background to make neon pop */
    color: #e0e0e0;
    font-size: 16px; /* Base font size, good for accessibility */
}

/* Base unit for responsiveness - using rem for scalable fonts and spacing */
html {
    font-size: 100%; /* Default to 16px */
}

/* Container & Overall Layout */
.exercise-list-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem 1.5rem; /* Adjusted padding using rem */
    background-color: #f1f2f6; /* Fallback, overridden by JS dynamic image */
    font-family: 'Poppins', sans-serif;
    min-height: 100vh;
    box-sizing: border-box;
    position: relative; /* For background image scaling */
    overflow-x: hidden; /* Prevent horizontal scroll */
}

/* Category Heading */
.category-heading {
    font-size: 3.5rem;
    font-weight: bold;
    color: #d5dfd5;
    margin-bottom: 1rem;
    text-shadow: 3px 3px 6px rgba(0, 0, 0, 0.5);
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 0.125rem; /* Adjusted letter-spacing */
    line-height: 1.2; /* Better readability */
}

/* Exercise Buttons */
.exercise-buttons {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 1rem; /* Adjusted gap using rem */
    margin-top: 1.5rem; /* Adjusted margin using rem */
    width: 100%; /* Take full width within container */
    max-width: 75rem; /* Max-width for desktop */
    padding: 0 1rem; /* Add some horizontal padding */
    box-sizing: border-box;
}

.exercise-item {
    flex-basis: auto; /* Allow items to determine their width */
    /* On smaller screens, buttons might be too wide, so let's adjust */
    flex-grow: 1; /* Allow them to grow */
    min-width: 8rem; /* Minimum width for buttons */
    max-width: 15rem; /* Maximum width for buttons */
}

.exercise-button {
    padding: 0.75rem 1.5rem; /* Adjusted padding using rem */
    font-size: 1.1rem; /* Adjusted font size */
    background-color: #16a085;
    color: white;
    border: none;
    cursor: pointer;
    border-radius: 1.5rem; /* Adjusted border-radius */
    box-shadow: 0 0 0.75rem rgba(0, 255, 255, 0.8), 0 0 1.5rem rgba(0, 255, 255, 0.6);
    transition: all 0.3s ease-in-out;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 0.0625rem; /* Adjusted letter-spacing */
    width: 100%; /* Make buttons fill their flex-basis */
    box-sizing: border-box;
}

.exercise-button:hover {
    background-color: #1abc9c;
    box-shadow: 0 0 1.25rem rgba(0, 255, 255, 1), 0 0 2.2rem rgba(0, 255, 255, 0.8);
    transform: scale(1.05);
}

.exercise-button.active {
    background-color: #1abc9c;
    box-shadow: 0 0 1.125rem rgba(0, 255, 255, 1), 0 0 1.75rem rgba(0, 255, 255, 0.8);
    border: 0.125rem solid #0ff;
}

/* Exercise Details Section - CONTAINER FOR AI BOX, DAILY, WEEKLY */
.exercise-details-container {
    margin-top: 2rem; /* Adjusted margin */
    width: 100%; /* Take full width within container */
    max-width: 75rem; /* Max-width for desktop */
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: stretch;
    gap: 1.5rem; /* Adjusted gap */
    box-sizing: border-box;
    padding: 0 1rem; /* Add horizontal padding */
}

/* THE MAIN EXERCISE DETAILS BOX (YOUR WORKOUT BOX) */
.exercise-details {
    background-color: rgba(0, 0, 0, 0.85);
    padding: 1.5rem; /* Adjusted padding */
    border-radius: 1.25rem;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex: 2 1 0%;
    min-width: 18.75rem; /* ~300px, good for desktop */
    height: 32.5rem; /* ~520px Fixed height for consistent look. Consider removing or adjusting in mobile media queries! */
    overflow-y: auto;
    border: 0.125rem solid #0ff;
    box-shadow: 0 0 0.75rem #0ff, 0 0 1.5rem #0ff;
    transition: all 0.3s ease-in-out;
}

.exercise-info {
    margin-bottom: 1rem;
}

.exercise-name {
    font-size: 1.8rem; /* Adjusted for elegance */
    color: #0ff;
    margin-bottom: 0.625rem;
    margin-top: 0.5rem;
    padding: 0.5rem 1rem;
    background-color: #001f3f;
    border: 0.125rem solid #0ff;
    border-radius: 1.25rem;
    cursor: pointer;
    text-align: center;
    box-shadow: 0 0 0.5rem #0ff, 0 0 0.9375rem #0ff;
    text-shadow: 0 0 0.25rem #0ff, 0 0 0.5rem #0ff;
    transition: box-shadow 0.3s ease, transform 0.2s ease;
    text-transform: uppercase;
    letter-spacing: 0.0625rem;
}

.exercise-name:hover {
    box-shadow: 0 0 0.75rem #0ff, 0 0 1.5rem #0ff, 0 0 2.2rem #0ff;
    transform: scale(1.02);
}

.description,
.benefits,
.calories-burned,
.duration,
.instructions,
.goal {
    font-size: 1rem; /* Adjusted */
    margin: 0.4375rem 0; /* Adjusted margin */
    color: #e0e0e0;
}
.description strong, .benefits strong, .calories-burned strong, .duration strong, .instructions strong, .goal strong {
    color: #4CAF50;
}

/* Timer & Button Controls */
.exercise-timer {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 1rem;
    color: #ee8813;
    font-size: 1.5rem; /* Adjusted */
    font-weight: bold;
    text-shadow: 0 0 0.25rem #ee8813;
}

.timer-controls {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 0.5rem;
    margin-top: 0.625rem;
}

.start-timer-btn,
.reset-timer-btn,
.goal-btn {
    background-color: #771f5a;
    color: white;
    padding: 0.6rem 1.25rem; /* Adjusted padding */
    border: none;
    border-radius: 1.25rem;
    cursor: pointer;
    transition: background-color 0.3s ease, box-shadow 0.3s ease, transform 0.3s ease;
    font-size: 0.9rem; /* Adjusted */
    font-weight: 600;
    text-transform: uppercase;
    box-shadow: 0 0 0.5rem rgba(119, 31, 90, 0.7);
}

.start-timer-btn:hover,
.reset-timer-btn:hover {
    background-color: #5db91f;
    transform: scale(1.05);
    box-shadow: 0 0 0.75rem rgba(93, 185, 31, 0.9);
}

.goal-btn:hover:not(:disabled) {
    background-color: #28a745;
    transform: scale(1.05);
    box-shadow: 0 0 0.75rem rgba(40, 167, 69, 0.9);
}

/* When Goal is Marked Achieved */
.goal-btn.completed {
    background-color: #28a745 !important;
    color: white;
    box-shadow: 0 0 0.5rem 0.1875rem rgba(40, 167, 69, 0.7);
    font-weight: bold;
    cursor: default;
    animation: pulse-green 1.5s infinite alternate;
}

@keyframes pulse-green {
    from { box-shadow: 0 0 0.5rem rgba(40, 167, 69, 0.5); }
    to { box-shadow: 0 0 1.25rem rgba(40, 167, 69, 1); }
}

.goal-btn:disabled {
    background-color: #555;
    cursor: not-allowed;
    opacity: 0.7;
}

/* Daily Results Section */
.daily-results {
    text-align: center;
    background-color: rgba(0, 0, 0, 0.85);
    border-radius: 1.25rem;
    padding: 1.25rem;
    border: 0.125rem solid #FFD700;
    box-shadow: 0 0 0.75rem #FFD700, 0 0 1.5rem #FFD700;
    flex: 1 1 0%;
    min-width: 11.25rem; /* ~180px */
}

/* Weekly Results & Progress Bars */
.weekly-results {
    text-align: center;
    background-color: rgba(0, 0, 0, 0.85);
    border-radius: 1.25rem;
    padding: 1.25rem;
    border: 0.125rem solid #EE8813;
    box-shadow: 0 0 0.75rem #EE8813, 0 0 1.5rem #EE8813;
    flex: 1 1 0%;
    min-width: 11.25rem; /* ~180px */
    margin-top: 0;
}

.progress-bar {
    background-color: #333;
    border-radius: 0.9375rem;
    width: 100%;
    height: 1.25rem;
    margin: 0.625rem auto;
    box-shadow: inset 0 0.0625rem 0.1875rem rgba(0, 0, 0, 0.3);
    overflow: hidden;
}

.progress {
    height: 100%;
    background-color: #16a085;
    border-radius: 0.9375rem;
    width: 0%;
    transition: width 1s ease-out;
    box-shadow: 0 0 0.375rem rgba(0, 255, 255, 0.7);
}

/* Heading Styles (used for sections such as Daily/Weekly Results) */
h3 {
    font-size: 1.5rem; /* Adjusted for elegance */
    color: #0ff;
    margin-bottom: 0.625rem;
    margin-top: 0.5rem;
    padding: 0.5rem 1rem;
    background-color: #001f3f;
    border: 0.125rem solid #0ff;
    border-radius: 1.25rem;
    cursor: pointer;
    text-align: center;
    box-shadow: 0 0 0.5rem #0ff, 0 0 0.9375rem #0ff;
    text-shadow: 0 0 0.25rem #0ff, 0 0 0.5rem #0ff;
    transition: box-shadow 0.3s ease, transform 0.2s ease;
    text-transform: uppercase;
    letter-spacing: 0.0625rem;
}

h3:hover {
    box-shadow: 0 0 0.75rem #0ff, 0 0 1.5rem #0ff, 0 0 2.2rem #0ff;
    transform: scale(1.02);
}

/* Paragraph Text */
p {
    font-size: 0.95rem; /* Adjusted for elegance */
    color: #eee;
    margin: 0.3125rem 0;
}

p strong {
    color: #FFD700;
}

/* Celebration Glow Effect for Exercise Details when a goal is achieved */
.exercise-details.celebration {
    border: 0.125rem solid #28a745;
    box-shadow: 0 0 1.125rem rgba(40, 167, 69, 0.7);
    animation: pulse-glow 1.5s infinite alternate;
}

@keyframes pulse-glow {
    from {
        box-shadow: 0 0 0.5rem rgba(40, 167, 69, 0.5);
    }
    to {
        box-shadow: 0 0 1.375rem rgba(40, 167, 69, 1);
    }
}

/* Completed Progress Bar Animation */
.progress-bar.complete .progress {
    background: linear-gradient(90deg, #2ecc71, #27ae60, #2ecc71);
    animation: pulseBar 1.5s infinite;
}

@keyframes pulseBar {
    0%, 100% {
        filter: brightness(1);
    }
    50% {
        filter: brightness(1.5);
    }
}

/* Task Checkboxes */
.task-checkboxes {
    margin-top: 1.25rem;
    background-color: rgba(0, 0, 0, 0.85);
    border: 0.125rem solid cyan;
    border-radius: 0.9375rem;
    padding: 0.9375rem;
    max-width: 15.625rem; /* ~250px */
    margin-left: auto;
    margin-right: auto;
    box-shadow: 0 0 1.125rem cyan;
    text-align: left;
}

.task-checkboxes label {
    display: flex;
    align-items: center;
    margin-bottom: 0.5rem;
    font-weight: bold;
    color: white;
    font-size: 0.9rem;
    cursor: pointer;
    transition: color 0.2s ease;
}

.task-checkboxes label:hover {
    color: #0ff;
}

.task-checkboxes input[type="checkbox"] {
    margin-right: 0.5rem;
    transform: scale(1.1); /* Slightly adjusted */
    accent-color: #0ff;
    cursor: pointer;
}

/* --- AI WORKOUT DETAILS BOX STYLES (FOR THE INNER SCROLLING TEXT CONTENT) --- */
.ai-workout-details-box {
    background-color: rgba(0, 0, 0, 0.7);
    border: 0.125rem solid #00f0ff;
    border-radius: 0.625rem;
    padding: 1rem;
    margin-top: 1rem;
    box-shadow: 0 0 0.75rem rgba(0, 240, 255, 0.6);
    text-align: left;
    color: #e0e0e0;
    flex-grow: 1;
    max-height: none;
    overflow-y: auto;
    line-height: 1.4;
}

/* Styles for the actual AI generated content within the inner box */
.ai-content-display {
    font-size: 1em;
    line-height: 1.5;
    white-space: pre-wrap;
    word-wrap: break-word;
    text-align: left;
}

.ai-content-display strong {
    color: #4CAF50;
}
.ai-content-display ul {
    list-style-type: '👉 ';
    padding-left: 1.125rem;
    margin-top: 0.375rem;
}
.ai-content-display li {
    margin-bottom: 0.3125rem;
}
.ai-content-display p {
    margin-bottom: 0.5rem;
}

/* Styles for the loading text within the AI box */
.loading-text {
    text-align: center;
    color: #FFD700;
    font-style: italic;
    font-size: 1rem;
    padding: 0.625rem;
}

/* --- NEW ASK VEERU SECTION STYLES --- */
.ask-veeru-container {
    background-color: rgba(0, 0, 0, 0.9);
    border: 0.125rem solid #0ff;
    border-radius: 1.25rem;
    padding: 1.25rem;
    margin-top: 2.5rem; /* Increased margin to separate from content above */
    margin-bottom: 2.5rem;
    width: 100%; /* Take full width within container */
    max-width: 37.5rem; /* ~600px */
    margin-left: auto;
    margin-right: auto;
    box-shadow: 0 0 1.125rem #0ff, 0 0 2.2rem #0ff;
    color: #fff;
    box-sizing: border-box;
    padding: 1.5rem; /* Ensure consistent padding */
}

.ask-veeru-container h3 {
    color: #4CAF50;
    text-align: center;
    margin-bottom: 1.125rem;
    font-size: 1.8rem; /* Adjusted */
    text-shadow: 0 0 0.5rem #4CAF50;
    border: 0.125rem solid #4CAF50;
    box-shadow: 0 0 0.5rem #4CAF50, 0 0 0.9375rem #4CAF50;
}

.ask-veeru-chat-display {
    background-color: rgba(255, 255, 255, 0.08);
    border: 0.0625rem solid rgba(255, 255, 255, 0.2);
    border-radius: 0.625rem;
    padding: 0.625rem;
    max-height: 17.5rem; /* ~280px */
    overflow-y: auto;
    margin-bottom: 0.9375rem;
    box-shadow: inset 0 0 0.5rem rgba(0, 255, 255, 0.3);
}

.chat-message {
    margin-bottom: 0.5rem;
    padding: 0.4375rem 0.625rem;
    border-radius: 0.5rem;
    line-height: 1.4;
    word-wrap: break-word;
    animation: fadeIn 0.5s ease-out;
    font-size: 0.9rem;
}

@keyframes fadeIn {
    from { opacity: 0; transform: translateY(0.5rem); }
    to { opacity: 1; transform: translateY(0); }
}

.chat-message.user {
    background-color: #2a2a2a;
    text-align: right;
    margin-left: 12%;
    border-top-right-radius: 0;
    border: 0.0625rem solid #66f;
    box-shadow: 0 0 0.25rem rgba(102, 102, 255, 0.5);
}

.chat-message.ai {
    background-color: #3a3a3a;
    text-align: left;
    margin-right: 12%;
    border-top-left-radius: 0;
    border: 0.0625rem solid #0ff;
    box-shadow: 0 0 0.25rem rgba(0, 255, 255, 0.5);
}

.chat-message strong {
    color: #FFD700;
    font-weight: bold;
}

.ask-veeru-input-area {
    display: flex;
    gap: 0.625rem;
    margin-top: 0.9375rem;
}

.ask-veeru-input-area input {
    flex-grow: 1;
    padding: 0.5625rem 0.75rem;
    border-radius: 0.5rem;
    border: 0.125rem solid #555;
    background-color: #222;
    color: #eee;
    font-size: 0.95em;
    outline: none;
    transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

.ask-veeru-input-area input::placeholder {
    color: #999;
}

.ask-veeru-input-area input:focus {
    border-color: #0ff;
    box-shadow: 0 0 0.5rem #0ff;
}

.ask-veeru-input-area button {
    padding: 0.5625rem 1.25rem;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 0.5rem;
    cursor: pointer;
    font-size: 0.95em;
    font-weight: 600;
    transition: background-color 0.3s ease, box-shadow 0.3s ease;
    box-shadow: 0 0 0.375rem rgba(76, 175, 80, 0.7);
}

.ask-veeru-input-area button:hover:not(:disabled) {
    background-color: #5cb85c;
    box-shadow: 0 0 0.625rem rgba(76, 175, 80, 1);
}

.ask-veeru-input-area button:disabled {
    background-color: #666;
    cursor: not-allowed;
    opacity: 0.7;
    box-shadow: none;
}

/* Loading dots animation */
@keyframes dots {
    0%, 20% {
        color: rgba(0,0,0,0);
        text-shadow:
            .25em 0 0 rgba(0,0,0,0),
            .5em 0 0 rgba(0,0,0,0);
    }
    40% {
        color: white;
        text-shadow:
            .25em 0 0 rgba(0,0,0,0),
            .5em 0 0 rgba(0,0,0,0);
    }
    60% {
        text-shadow:
            .25em 0 0 white,
            .5em 0 0 rgba(0,0,0,0);
    }
    80%, 100% {
        text-shadow:
            .25em 0 0 white,
            .5em 0 0 white;
    }
}
.loading-dots:after {
    content: ' .';
    animation: dots 1s steps(5, end) infinite;
}

.loading-message {
    font-style: italic;
    color: #bbb;
}

/* --- MEDIA QUERIES FOR RESPONSIVENESS --- */

/* For devices between ~769px and ~1023px (e.g., larger tablets in portrait or landscape) */
@media (max-width: 1023px) {
    html {
        font-size: 90%; /* Scale down base font size slightly */
    }
    .exercise-list-container {
        padding: 1.5rem 1rem;
    }
    .category-heading {
        font-size: 2.8rem;
    }
    .exercise-details-container {
        flex-direction: column; /* Stack all items vertically */
        align-items: center;
        gap: 1.5rem; /* Space between stacked items */
        padding: 0 1rem; /* Ensure padding on sides */
    }
    .exercise-details, .daily-results, .weekly-results, .ask-veeru-container {
        width: 100%; /* Take full width within container */
        max-width: 37.5rem; /* Constrain max-width for readability */
        height: auto; /* Allow height to adjust */
        min-height: unset; /* Remove fixed min-heights */
        flex-basis: auto; /* Reset flex basis */
        min-width: unset; /* Allow shrinking */
        max-width: 37.5rem; /* Same max-width */
        padding: 1.25rem; /* Adjust padding */
    }
    .exercise-details {
        min-height: 25rem; /* Give it a minimum height to avoid squishing */
    }
    .exercise-name, h3 {
        font-size: 1.6rem;
    }
    .ai-workout-details-box {
        max-height: 15rem; /* Re-introduce max-height for inner scroll when stacked */
        flex-grow: unset; /* Remove flex-grow when stacking */
    }
    .ask-veeru-container {
        margin-top: 1.5rem;
        margin-bottom: 1.5rem;
    }
}

/* For devices between ~481px and ~768px (e.g., smaller tablets, large phones in landscape) */
@media (max-width: 768px) {
    html {
        font-size: 85%; /* Further scale down base font size */
    }
    .category-heading {
        font-size: 2.2rem;
    }
    .exercise-button {
        font-size: 0.9rem;
        padding: 0.6rem 1.2rem;
        min-width: 7rem;
    }
    .exercise-details, .daily-results, .weekly-results, .ask-veeru-container {
        padding: 1rem;
    }
    .exercise-name, h3 {
        font-size: 1.4rem;
    }
    .exercise-timer {
        font-size: 1.3rem;
    }
    .start-timer-btn, .reset-timer-btn, .goal-btn {
        font-size: 0.8rem;
        padding: 0.5rem 1rem;
    }
    .task-checkboxes label {
        font-size: 0.8rem;
    }
    .ask-veeru-container h3 {
        font-size: 1.6rem;
    }
    .ai-content-display, .chat-message {
        font-size: 0.85rem;
    }
    .ask-veeru-input-area input, .ask-veeru-input-area button {
        font-size: 0.85rem;
        padding: 0.5rem 0.75rem;
    }
    .chat-message.user {
        margin-left: 10%;
    }
    .chat-message.ai {
        margin-right: 10%;
    }
}

/* For devices up to ~480px (e.g., most mobile phones) */
@media (max-width: 480px) {
    html {
        font-size: 80%; /* Further scale down base font size */
    }
    .exercise-list-container {
        padding: 1rem 0.5rem; /* Reduce overall padding */
    }
    .category-heading {
        font-size: 1.8rem;
        margin-bottom: 0.8rem;
    }
    .exercise-buttons {
        gap: 0.8rem;
        padding: 0 0.5rem;
    }
    .exercise-item {
        min-width: unset; /* Allow full flexibility */
        max-width: unset;
    }
    .exercise-button {
        font-size: 0.8rem;
        padding: 0.6rem 1rem;
    }
    .exercise-details-container {
        gap: 1rem; /* Reduce gap when stacked */
        padding: 0 0.5rem;
    }
    .exercise-details, .daily-results, .weekly-results, .ask-veeru-container {
        padding: 0.8rem;
        min-height: unset; /* Ensure full flexibility */
        max-width: 95%; /* Allow it to be slightly less than full width for edge spacing */
    }
    .exercise-details {
        min-height: 20rem; /* A minimum height for the main box */
    }
    .exercise-name, h3 {
        font-size: 1.2rem;
        padding: 0.4rem 0.8rem;
    }
    .exercise-timer {
        font-size: 1.1rem;
    }
    .timer-controls {
        flex-direction: column; /* Stack buttons vertically */
        gap: 0.4rem;
        width: 100%; /* Make controls full width */
    }
    .start-timer-btn, .reset-timer-btn, .goal-btn {
        width: 100%; /* Make buttons full width */
        font-size: 0.8rem;
        padding: 0.5rem 0.8rem;
    }
    .task-checkboxes {
        padding: 0.8rem;
        max-width: 90%; /* Adjust max width for very small screens */
    }
    .task-checkboxes label {
        font-size: 0.75rem;
    }
    .task-checkboxes input[type="checkbox"] {
        transform: scale(0.9);
    }
    .ask-veeru-container {
        margin-top: 1rem;
        margin-bottom: 1rem;
        padding: 1rem;
    }
    .ask-veeru-container h3 {
        font-size: 1.4rem;
    }
    .ai-content-display, .chat-message {
        font-size: 0.75rem;
    }
    .ask-veeru-input-area {
        flex-direction: column; /* Stack input and button */
        gap: 0.4rem;
    }
    .ask-veeru-input-area input, .ask-veeru-input-area button {
        width: 100%; /* Full width for input and button */
        font-size: 0.8rem;
        padding: 0.4rem 0.6rem;
    }
    .chat-message.user {
        margin-left: 5%;
    }
    .chat-message.ai {
        margin-right: 5%;
    }
}

/* Landscape mode for phones */
@media (max-height: 500px) and (max-width: 800px) and (orientation: landscape) {
    .exercise-list-container {
        padding: 0.8rem;
    }
