.homepage-container {
 
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 100vh;
  width: 100%;
  position: relative;
  color: white;
  overflow: hidden;
}

/* Center "GYM" */
.center-gym {
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 7rem;
  font-weight: 900;
  z-index: 2;
  text-shadow: 3px 3px 6px black;
}

/* Left of "GYM" */
.left-heading {
  position: absolute;
  top: 38%;
  left: 6%;
  transform: translateY(-50%);
  font-size: 3.5rem;
  font-weight: bold;
  z-index: 2;
  white-space: nowrap;
  text-shadow: 2px 2px 4px black;
}

/* Below GYM */
.bottom-left-heading {
  position: absolute;
  top: 66%;
  left: 70%;
  transform: translateX(-50%);
  font-size: 2.6rem;
  font-style: italic;
  z-index: 2;
  white-space: nowrap;
  text-shadow: 1px 1px 3px black;
}

/* Responsive */
@media (max-width: 768px) {
  .center-gym {
    font-size: 4rem;
  }

  .left-heading {
    font-size: 2rem;
    left: 10%;
  }

  .bottom-left-heading {
    font-size: 1rem;
    top: 62%;
  }
}
