/* src/pages/Testimonials.css */
.testimonials-page {
    padding: 3rem 1.5rem;
    background-color: #1a1a2e; /* Dark background */
    color: #e0e0e0;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
}

.page-heading {
    font-size: 3.5rem;
    font-weight: bold;
    color: #0ff; /* Neon Cyan */
    margin-bottom: 1rem;
    text-shadow: 0 0 0.5rem #0ff, 0 0 1rem #0ff;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 0.125rem;
    line-height: 1.2;
}

.page-subheading {
    font-size: 1.2rem;
    color: #e0e0e0;
    margin-bottom: 2.5rem;
    text-align: center;
    max-width: 600px;
}

.testimonials-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 1.5rem;
    width: 100%;
    max-width: 1200px;
    padding: 0 1rem;
    box-sizing: border-box;
}

.testimonial-card {
    background-color: rgba(0, 0, 0, 0.7);
    border: 2px solid #4CAF50; /* Green neon border */
    border-radius: 1.25rem;
    padding: 1.5rem;
    text-align: center;
    box-shadow: 0 0 0.75rem #4CAF50, 0 0 1.5rem rgba(76, 175, 80, 0.7);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.testimonial-card:hover {
    transform: translateY(-0.5rem);
    box-shadow: 0 0 1rem #4CAF50, 0 0 2rem rgba(76, 175, 80, 0.9);
}

.testimonial-image {
    width: 6.25rem; /* 100px */
    height: 6.25rem; /* 100px */
    border-radius: 50%;
    margin-bottom: 1rem;
    border: 3px solid #0ff; /* Cyan border */
    box-shadow: 0 0 0.5rem #0ff, 0 0 1rem rgba(0, 255, 255, 0.5);
    background-color: rgba(0, 255, 255, 0.15); /* <<< Adjusted: Slightly more opaque cyan background for visibility */
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    flex-shrink: 0; /* Prevent the circle from shrinking */
}

/* Style for the name INSIDE the circle */
.testimonial-image .image-name {
    color: #ffffff; /* White text for maximum visibility */
    font-size: 1.2rem; /* <<< Adjusted: Slightly larger font size */
    font-weight: bold;
    text-transform: uppercase;
    text-shadow: 0 0 0.3rem #00ffff, 0 0 0.8rem rgba(0, 255, 255, 0.8); /* <<< Adjusted: Stronger neon glow */
    line-height: 1; /* Prevent extra spacing around text */
    padding: 0.2rem; /* Small padding inside the circle */
    text-align: center;
    white-space: nowrap; /* Prevent name from wrapping inside circle */
}


.testimonial-quote {
    font-size: 1.1rem;
    font-style: italic;
    margin-bottom: 1rem;
    color: #f8f8f8;
    line-height: 1.5;
}

.testimonial-author {
    font-weight: bold;
    color: #00ffff; /* Bright Cyan for better visibility */
    font-size: 1.1rem;
    margin-top: auto;
    margin-bottom: 0.25rem;
    text-shadow: 0 0 0.2rem #00ffff, 0 0 0.5rem rgba(0, 255, 255, 0.5);
}

.testimonial-title {
    font-size: 0.95rem;
    color: #90ee90; /* Light green for better visibility and a different shade of neon */
    text-shadow: 0 0 0.1rem #90ee90, 0 0 0.3rem rgba(144, 238, 144, 0.5);
}

/* Responsive adjustments for Testimonials */
@media (max-width: 768px) {
    .page-heading {
        font-size: 2.5rem;
    }
    .page-subheading {
        font-size: 1rem;
        margin-bottom: 1.5rem;
    }
    .testimonials-grid {
        grid-template-columns: 1fr;
        padding: 0 0.5rem;
    }
    .testimonial-card {
        padding: 1rem;
    }
    .testimonial-image {
        width: 5rem;
        height: 5rem;
    }
    .testimonial-image .image-name {
        font-size: 1.0rem; /* Adjusted font size for smaller screens */
    }
    .testimonial-quote {
        font-size: 1rem;
    }
    .testimonial-author {
        font-size: 1rem;
    }
    .testimonial-title {
        font-size: 0.85rem;
    }
}

@media (max-width: 480px) {
    .testimonials-page {
        padding: 2rem 0.5rem;
    }
    .page-heading {
        font-size: 2rem;
    }
    .page-subheading {
        font-size: 0.9rem;
    }
    .testimonial-card {
        padding: 0.8rem;
    }
    .testimonial-image {
        width: 4rem;
        height: 4rem;
    }
    .testimonial-image .image-name {
        font-size: 0.8rem; /* Adjusted font size for very small screens */
    }
    .testimonial-quote {
        font-size: 0.9rem;
    }
    .testimonial-author {
        font-size: 0.9rem;
    }
    .testimonial-title {
        font-size: 0.8rem;
    }
}