/* src/App.css */

/* --- GENERAL NAVBAR STYLES (for both desktop and mobile as base) --- */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #0d0d21; /* Keep dark background */
    padding: 0.8rem 1rem;
    margin: 0;
    width: 100%;
    box-sizing: border-box;
    position: sticky;
    top: 0;
    z-index: 1000;
    box-shadow: 0 4px 20px rgba(0, 255, 255, 0.2);
}

.navbar-brand {
    color: #0ff;
    font-size: 1.5rem;
    font-weight: bold;
    text-decoration: none;
    padding: 0.7rem 1.2rem;
    border: 0.15rem solid #0ff;
    border-radius: 0.625rem;
    background-color: rgba(0, 0, 0, 0.6);
    box-shadow: 0 0 0.8rem #0ff, 0 0 1.5rem rgba(0, 255, 255, 0.3);
    text-shadow: 0 0 0.4rem #0ff;
    transition: all 0.3s ease-in-out;
    margin-right: 1.5rem;
    flex-shrink: 0;
    white-space: nowrap;
    letter-spacing: 0.05rem;
}

.navbar-brand:hover {
    background-color: rgba(0, 255, 255, 0.1);
    box-shadow: 0 0 1rem #0ff, 0 0 2rem rgba(0, 255, 255, 0.5), 0 0 3rem rgba(0, 255, 255, 0.2);
    text-shadow: 0 0 0.5rem #0ff;
    transform: translateY(-0.125rem);
}

.nav-links {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex; /* Display horizontally by default (desktop) */
    gap: 1rem; /* Space between links */
}

.nav-links li .nav-item {
    display: block;
    padding: 0.75rem 1rem;
    color: white;
    text-decoration: none;
    font-weight: bold;
    font-size: 1rem;
    border: 0.125rem solid #0ff;
    border-radius: 0.625rem;
    background-color: rgba(0, 0, 0, 0.6);
    box-shadow: 0 0 0.625rem #0ff;
    transition: all 0.3s ease-in-out;
    box-sizing: border-box;
    text-transform: uppercase;
    letter-spacing: 0.05rem;
    white-space: nowrap;
    cursor: pointer;
    min-width: 80px;
    text-align: center; /* Center text within buttons */
}

.nav-links li .nav-item:hover {
    background-color: rgba(0, 255, 255, 0.1);
    color: #fff;
    box-shadow: 0 0 0.8rem #0ff, 0 0 1.5rem rgba(0, 255, 255, 0.6), 0 0 2rem rgba(0, 255, 255, 0.3);
    transform: translateY(-0.125rem);
}

.nav-links li .nav-item.active,
.nav-links li .nav-item:active {
    background-color: rgba(0, 128, 128, 0.4);
    color: white;
    border-color: #00ffff;
    box-shadow:
        0 0 1rem #00ffff,
        0 0 2rem rgba(0, 255, 255, 0.7),
        inset 0 0 0.5rem #00ffff;
    transform: scale(0.98);
    animation: pulse-neon 1.5s infinite alternate;
}

@keyframes pulse-neon {
    from {
        box-shadow: 0 0 1rem #00ffff, 0 0 2rem rgba(0, 255, 255, 0.6);
    }
    to {
        box-shadow: 0 0 1.2rem #00ffff, 0 0 2.5rem rgba(0, 255, 255, 0.8);
    }
}

/* --- HAMBURGER MENU STYLES --- */
.hamburger-menu {
    display: none; /* Hidden by default on desktop */
    flex-direction: column;
    justify-content: space-between;
    width: 30px;
    height: 25px;
    cursor: pointer;
    z-index: 1001; /* Ensure it's above other content */
}

.hamburger-menu span {
    display: block;
    width: 100%;
    height: 3px;
    background-color: #0ff; /* Neon color for hamburger lines */
    border-radius: 2px;
    transition: all 0.3s ease-in-out;
}

/* --- MEDIA QUERIES FOR MOBILE --- */
@media (max-width: 768px) { /* Apply these styles when screen width is 768px or less */
    .navbar {
        padding: 0.5rem 1rem; /* Slightly less padding on smaller screens */
    }

    .navbar-brand {
        font-size: 1.2rem; /* Smaller brand text */
        padding: 0.5rem 0.8rem;
        margin-right: 0.5rem; /* Reduce margin */
    }

    .nav-links {
        display: none; /* Hide horizontal nav links by default on mobile */
        flex-direction: column; /* Stack links vertically */
        position: absolute; /* Position the menu absolutely */
        top: 100%; /* Below the navbar */
        left: 0;
        width: 100%;
        background-color: #0d0d21; /* Same background as navbar */
        box-shadow: 0 8px 16px rgba(0, 255, 255, 0.3); /* Stronger shadow when open */
        padding: 1rem 0; /* Vertical padding for menu items */
        text-align: center;
        gap: 0; /* Remove gap when stacked */
    }

    .nav-links.open {
        display: flex; /* Show the menu when 'open' class is added by JS */
    }

    .nav-links li {
        width: 100%; /* Make each list item take full width */
        margin-bottom: 0.5rem; /* Space between vertical links */
    }
    .nav-links li:last-child {
        margin-bottom: 0;
    }

    .nav-links li .nav-item {
        width: 80%; /* Button width within the full-width list item */
        margin: 0.5rem auto; /* Center the buttons */
        font-size: 0.9rem; /* Smaller font size for mobile buttons */
        padding: 0.6rem 0.8rem;
    }

    .hamburger-menu {
        display: flex; /* Show hamburger icon on mobile */
    }

    /* Animation for hamburger icon to 'X' */
    .hamburger-menu.open span:nth-child(1) {
        transform: translateY(11px) rotate(45deg);
    }

    .hamburger-menu.open span:nth-child(2) {
        opacity: 0;
    }

    .hamburger-menu.open span:nth-child(3) {
        transform: translateY(-11px) rotate(-45deg);
    }
}

/* --- OTHER GENERAL STYLES (kept for reference, ensure your full App.css includes these) --- */
html, body, #root {
    margin: 0;
    padding: 0;
    height: 100%;
    font-family: Arial, sans-serif;
    background-color: #0d0d21; /* Dark background */
    color: white;
    box-sizing: border-box;
}

/* Add global box-sizing for consistency */
* {
    box-sizing: border-box;
}

/* Basic body styling to prevent scrollbar issues */
body {
    overflow-x: hidden; /* Prevent horizontal scrollbar unless explicitly needed */
}