/* Base Navbar Container */
.navbar {
    display: flex;
    justify-content: space-between; /* Keeps brand left, nav links block right */
    align-items: center;
    background-color: #0d0d21;
    padding: 0.8rem 1rem;
    margin: 0;
    width: 100%;
    box-sizing: border-box;
    position: sticky;
    top: 0;
    z-index: 1000;
    box-shadow: 0 4px 20px rgba(0, 255, 255, 0.4);
}

/* Navbar Brand (Logo/Title) - VEERU'S GYM STYLING */
.navbar-brand {
    color: #0ff;
    font-size: 1.5rem;
    font-weight: bold;
    text-decoration: none;
    padding: 0.7rem 1.2rem;
    border: 0.15rem solid #0ff;
    border-radius: 0.625rem;
    background-color: rgba(0, 0, 0, 0.6);
    box-shadow: 0 0 0.8rem #0ff, 0 0 1.5rem rgba(0, 255, 255, 0.5);
    text-shadow: 0 0 0.4rem #0ff;
    transition: all 0.3s ease-in-out;
    margin-right: 1.5rem; /* Space between brand and first nav item */
    flex-shrink: 0;
    white-space: nowrap;
    letter-spacing: 0.05rem;
}

.navbar-brand:hover {
    background-color: rgba(0, 255, 255, 0.2);
    box-shadow: 0 0 1.2rem #0ff, 0 0 2.5rem rgba(0, 255, 255, 0.7), 0 0 3.5rem rgba(0, 255, 255, 0.3);
    text-shadow: 0 0 0.6rem #0ff;
    transform: translateY(-0.125rem);
}

/* Navigation Links Container (including Logout) */
.nav-links {
    display: flex;
    flex-grow: 1; /* Allows nav links to take up available space */
    justify-content: flex-end; /* <<< Key Change: Align items to the end (right) */
    align-items: center;
    padding: 0;
    margin: 0;
    list-style: none;
    flex-wrap: nowrap; /* Ensure no wrapping on main desktop view */
    gap: 0.5rem; /* <<< Key Change: Add a smaller, consistent gap between items */
    /* You can adjust 0.5rem to 0.7rem or 0.3rem based on how tight you want them */
}

/* Individual Navigation Item (including Logout's li) */
.nav-links li {
    flex-shrink: 0; /* Prevent items from shrinking too much */
    text-align: center;
    padding: 0;
    /* No individual margins needed if gap is used */
}

/* Styling for all Nav Items (NavLink's a tag and Logout button) */
.nav-links li .nav-item {
    display: block;
    padding: 0.75rem 1rem;
    color: white;
    text-decoration: none;
    font-weight: bold;
    font-size: 1rem;
    border: 0.125rem solid #0ff;
    border-radius: 0.625rem;
    background-color: rgba(0, 0, 0, 0.6);
    box-shadow: 0 0 0.625rem #0ff;
    transition: all 0.3s ease-in-out;
    box-sizing: border-box;
    text-transform: uppercase;
    letter-spacing: 0.05rem;
    white-space: nowrap;
    cursor: pointer;
    min-width: 80px; /* Ensure a minimum width */
}

/* Hover Effect for all Nav Items (including Logout) */
.nav-links li .nav-item:hover {
    background-color: rgba(0, 255, 255, 0.2);
    color: #fff;
    box-shadow: 0 0 1rem #fff,
                0 0 2rem rgba(0, 255, 255, 0.8),
                0 0 3rem rgba(0, 255, 255, 0.5);
    transform: translateY(-0.125rem);
}

/* ACTIVE (ON CLICK / CURRENT PAGE) EFFECT for all Nav Items (including Logout) */
.nav-links li .nav-item.active,
.nav-links li .nav-item:active {
    background-color: #008080;
    color: white;
    border-color: #00ffff;
    box-shadow:
        0 0 1.25rem #00ffff,
        0 0 2.5rem #00ffff,
        0 0 4rem #00ffff,
        0 0 6rem #00ffff,
        inset 0 0 1rem #00ffff;
    transform: scale(0.98);
    animation: pulse-neon 1.5s infinite alternate;
}

/* Optional: Animation for the active state to make it "live" */
@keyframes pulse-neon {
    from {
        box-shadow:
            0 0 1.25rem #00ffff,
            0 0 2.5rem #00ffff,
            0 0 4rem #00ffff,
            0 0 6rem rgba(0, 255, 255, 0.7);
    }
    to {
        box-shadow:
            0 0 1.5rem #00ffff,
            0 0 3rem #00ffff,
            0 0 5rem #00ffff,
            0 0 7rem rgba(0, 255, 255, 0.9);
    }
}

/* Responsive Adjustments */
@media (max-width: 992px) {
    .navbar {
        padding: 0.6rem 0.5rem;
    }
    .navbar-brand {
        font-size: 1.3rem;
        margin-right: 0.8rem; /* Reduced margin for smaller screens */
        padding: 0.5rem 0.8rem;
    }
    .nav-links {
        gap: 0.4rem; /* Slightly smaller gap on tablets */
    }
    .nav-links li .nav-item {
        font-size: 0.85rem; /* Smaller font size */
        padding: 0.5rem 0.7rem; /* Adjusted padding */
        min-width: 70px; /* Reduced min-width */
    }
}

@media (max-width: 768px) {
    .navbar {
        flex-direction: column; /* Stack if space is really tight */
        align-items: center;
        padding: 0.5rem;
    }
    .navbar-brand {
        margin-bottom: 0.5rem;
        margin-right: 0;
        font-size: 1.5rem;
        padding: 0.7rem 1rem;
    }
    .nav-links {
        flex-wrap: wrap; /* Allow wrapping onto multiple lines if needed */
        justify-content: center; /* Center items when wrapped */
        width: 100%;
        gap: 0.5rem; /* Maintain gap for wrapped items */
    }
    .nav-links li {
        flex-grow: 1;
        flex-basis: auto;
        min-width: 100px;
    }
    .nav-links li .nav-item {
        font-size: 0.85rem;
        padding: 0.7rem 0.5rem;
        border-width: 1px;
    }
    .nav-links li .nav-item.active,
    .nav-links li .nav-item:active {
        box-shadow:
            0 0 0.8rem #00ffff,
            0 0 1.5rem #00ffff,
            0 0 2rem #00ffff,
            0 0 3rem #00ffff,
            inset 0 0 0.5rem #00ffff;
    }
}

@media (max-width: 480px) {
    .navbar-brand {
        font-size: 1.2rem;
        padding: 0.5rem 0.6rem;
    }
    .nav-links li .nav-item {
        font-size: 0.75rem;
        padding: 0.5rem 0.3rem;
        min-width: 60px;
    }
}