/* src/pages/Contact.css */
.contact-page {
    padding: 3rem 1.5rem;
    background-color: #1a1a2e; /* Dark background */
    color: #e0e0e0;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
}

.page-heading {
    font-size: 3.5rem;
    font-weight: bold;
    color: #0ff; /* Neon Cyan */
    margin-bottom: 1rem;
    text-shadow: 0 0 0.5rem #0ff, 0 0 1rem #0ff;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 0.125rem;
    line-height: 1.2;
}

.page-subheading {
    font-size: 1.2rem;
    color: #e0e0e0;
    margin-bottom: 2.5rem;
    text-align: center;
    max-width: 600px;
}

.contact-content {
    display: flex;
    flex-wrap: wrap; /* Allow wrapping on smaller screens */
    gap: 2rem;
    width: 100%;
    max-width: 1000px;
    padding: 0 1rem;
    box-sizing: border-box;
    justify-content: center; /* Center items when they wrap */
}

.contact-info, .contact-form-container {
    background-color: rgba(0, 0, 0, 0.7);
    border: 2px solid #EE8813; /* Orange neon border */
    border-radius: 1.25rem;
    padding: 2rem;
    box-shadow: 0 0 0.75rem #EE8813, 0 0 1.5rem rgba(238, 136, 19, 0.7);
    flex: 1; /* Allow to grow and shrink */
    min-width: 300px; /* Minimum width for each section */
    max-width: 500px; /* Max width to prevent stretching too much */
    display: flex;
    flex-direction: column;
}

.contact-info h3, .contact-form-container h3 {
    font-size: 2rem;
    color: #4CAF50; /* Green heading */
    margin-bottom: 1.5rem;
    text-align: center;
    text-shadow: 0 0 0.5rem #4CAF50;
    border-bottom: 2px solid rgba(76, 175, 80, 0.5);
    padding-bottom: 0.5rem;
}

.contact-info p {
    font-size: 1rem;
    margin-bottom: 0.8rem;
    line-height: 1.5;
    color: #f0f0f0;
}

.contact-info strong {
    color: #FFD700; /* Gold */
}

.contact-form {
    display: flex;
    flex-direction: column;
    width: 100%;
    flex-grow: 1; /* Make form take available height */
}

.form-group {
    margin-bottom: 1.25rem;
}

.form-group label {
    display: block;
    font-size: 1rem;
    color: #0ff; /* Neon cyan for labels */
    margin-bottom: 0.5rem;
    font-weight: bold;
}

.form-group input,
.form-group textarea {
    width: 100%;
    padding: 0.75rem;
    border-radius: 0.5rem;
    border: 2px solid #555;
    background-color: #2a2a2a;
    color: #e0e0e0;
    font-size: 1rem;
    outline: none;
    box-sizing: border-box; /* Include padding in width */
    transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

.form-group input:focus,
.form-group textarea:focus {
    border-color: #0ff;
    box-shadow: 0 0 0.5rem #0ff;
}

.form-group textarea {
    resize: vertical; /* Allow vertical resizing */
    min-height: 5rem;
}

.submit-button {
    background-color: #4CAF50;
    color: white;
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius: 0.5rem;
    cursor: pointer;
    font-size: 1.1rem;
    font-weight: 600;
    margin-top: auto; /* Push button to bottom */
    transition: background-color 0.3s ease, box-shadow 0.3s ease, transform 0.2s ease;
    box-shadow: 0 0 0.5rem rgba(76, 175, 80, 0.7);
}

.submit-button:hover {
    background-color: #5cb85c;
    box-shadow: 0 0 0.75rem rgba(76, 175, 80, 1);
    transform: translateY(-2px);
}

/* Responsive adjustments for Contact */
@media (max-width: 768px) {
    .page-heading {
        font-size: 2.5rem;
    }
    .page-subheading {
        font-size: 1rem;
        margin-bottom: 1.5rem;
    }
    .contact-content {
        flex-direction: column; /* Stack columns on smaller screens */
        gap: 1.5rem;
        padding: 0 0.5rem;
    }
    .contact-info, .contact-form-container {
        padding: 1.5rem;
        min-width: unset; /* Remove min-width to allow shrinking */
        max-width: 100%; /* Take full width */
    }
    .contact-info h3, .contact-form-container h3 {
        font-size: 1.8rem;
        margin-bottom: 1rem;
    }
    .contact-info p {
        font-size: 0.95rem;
    }
    .form-group label {
        font-size: 0.95rem;
    }
    .form-group input,
    .form-group textarea {
        font-size: 0.95rem;
        padding: 0.6rem;
    }
    .submit-button {
        font-size: 1rem;
        padding: 0.6rem 1.2rem;
    }
}

@media (max-width: 480px) {
    .contact-page {
        padding: 2rem 0.5rem;
    }
    .page-heading {
        font-size: 2rem;
    }
    .page-subheading {
        font-size: 0.9rem;
    }
    .contact-info, .contact-form-container {
        padding: 1rem;
    }
    .contact-info h3, .contact-form-container h3 {
        font-size: 1.5rem;
        margin-bottom: 0.8rem;
    }
    .contact-info p {
        font-size: 0.85rem;
    }
    .form-group label {
        font-size: 0.85rem;
    }
    .form-group input,
    .form-group textarea {
        font-size: 0.85rem;
        padding: 0.5rem;
    }
    .submit-button {
        font-size: 0.9rem;
        padding: 0.5rem 1rem;
    }
}