.page-container {
  text-align: center;
  padding: 50px;
  position: relative;
}

.page-bg {
  width: 100%;
  height: 100vh;
  object-fit: cover;
  animation: rotate-bg 20s infinite;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

h1 {
  font-size: 3em;
  color: #fff;
  margin-top: 50px;
}

ul {
  list-style-type: none;
  padding: 0;
  font-size: 1.5em;
  color: #fff;
}

li {
  margin: 20px 0;
}

@keyframes rotate-bg {
  0% {
      background-image: url('../../images/bg1.png');
  }
  25% {
      background-image: url('../../images/bg2.png');
  }
  50% {
      background-image: url('../../images/bg3.png');
  }
  75% {
      background-image: url('../../images/bg4.png');
  }
  100% {
      background-image: url('../../images/bg5.png');
  }
}