.page-container {
  text-align: center;
  padding: 50px;
  position: relative;
  min-height: 100vh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  animation: rotate-bg 30s infinite;
  color: white;
}

h1 {
  font-size: 3em;
  margin-top: 40px;
  text-shadow: 2px 2px 5px #000;
  color: cyan;
}

p {
  font-size: 1.5em;
  font-weight: bold;
  margin-bottom: 30px;
  text-shadow: 1px 1px 3px #000;
  color: #00ffff;
}

ul {
  list-style-type: none;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

li {
  background: rgba(0, 0, 0, 0.7);
  padding: 15px 25px;
  border-radius: 10px;
  font-weight: bold;
  font-size: 1.2em;
  box-shadow: 0 0 15px cyan;
  transition: transform 0.3s ease;
}

li:hover {
  transform: scale(1.1);
}

/* Boys' gym rotating background */
.boys-gym {
  animation-name: rotate-bg-boys;
}


/* Background Animation */
@keyframes rotate-bg-boys {
  0%   { background-image: url('../../images/bg7.png'); }
  25%  { background-image: url('../../images/bg8.png'); }
  50%  { background-image: url('../../images/bg3.png'); }
  75%  { background-image: url('../../images/bg4.png'); }
  100% { background-image: url('../../images/bg5.png'); }
}
.button-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  margin-top: 30px;
}

.gym-category-button {
  background-color: black;
  color: white;
  border: 2px solid cyan;
  padding: 12px 24px;
  font-size: 1.1rem;
  border-radius: 10px;
  box-shadow: 0 0 10px cyan;
  transition: all 0.3s ease;
  cursor: pointer;
}

.gym-category-button:hover {
  background-color: cyan;
  color: black;
  box-shadow: 0 0 20px cyan, 0 0 40px cyan;
}



