/* src/pages/ExerciseDetail1.css */
.exercise-container {
  display: flex;
  gap: 2rem;
  padding: 2rem;
  background-color: #1a1a1a;
  color: white;
  min-height: 100vh;
}

.scrollable {
  max-height: 80vh;
  overflow-y: auto;
  padding: 1rem;
  background: #2c2c2c;
  border-radius: 10px;
}

.exercise-details-box {
  flex: 1;
}

.chatbot-box {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.chat-history {
  flex: 1;
  overflow-y: auto;
  margin-bottom: 1rem;
  border-top: 1px solid #444;
  padding-top: 1rem;
}

.chat-input {
  display: flex;
  gap: 0.5rem;
}

.chat-input input {
  flex: 1;
  padding: 0.5rem;
  border-radius: 5px;
  border: none;
}

.chat-input button {
  padding: 0.5rem 1rem;
  border: none;
  background-color: #4caf50;
  color: white;
  border-radius: 5px;
  cursor: pointer;
}
