.category-buttons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-top: 30px;
}

.neon-button {
  text-decoration: none;
  color: white;
  font-weight: bold;
  font-size: 1.4em;
  padding: 15px 25px;
  background-color: black;
  border-radius: 10px;
  box-shadow: 0 0 10px cyan, 0 0 20px cyan;
  transition: all 0.3s ease;
  text-align: center;
}

.neon-button:hover {
  transform: scale(1.05);
  box-shadow: 0 0 20px white, 0 0 30px cyan;
}




@keyframes rotate-bg-girls {
  0%   { background-image: url('../../images/bg6.png'); }
  25%  { background-image: url('../../images/bg7.png'); }
  50%  { background-image: url('../../images/bg8.png'); }
  75%  { background-image: url('../../images/bg1.png'); }
  100% { background-image: url('../../images/bg2.png'); }
}
.button-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  margin-top: 30px;
}

.gym-category-button {
  background-color: black;
  color: white;
  border: 2px solid cyan;
  padding: 12px 24px;
  font-size: 1.1rem;
  border-radius: 10px;
  box-shadow: 0 0 10px cyan;
  transition: all 0.3s ease;
  cursor: pointer;
}

.gym-category-button:hover {
  background-color: cyan;
  color: black;
  box-shadow: 0 0 20px cyan, 0 0 40px cyan;
}

