/* src/components/MusicPlayer.css */
.music-player {
    position: fixed;
    bottom: 15px;
    right: 15px;
    background-color: rgba(0, 0, 0, 0.75);
    color: white;
    padding: 5px 10px; /* Smaller padding */
    border-radius: 5px; /* Smaller border radius */
    box-shadow: 0 0 5px cyan; /* Softer shadow */
    z-index: 1000;
    display: flex;
    flex-direction: column;
    gap: 4px; /* Reduced gap */
    font-size: 12px; /* Smaller font size */
}

.music-player .controls {
    display: flex;
    align-items: center;
    gap: 6px; /* Adjusted gap for compactness */
}

.music-player button,
.music-player select,
.music-player input[type="range"] {
    background: black;
    color: white;
    border: none;
    border-radius: 3px; /* Slightly smaller radius */
    padding: 3px 5px; /* Smaller padding */
    font-size: 12px; /* Adjusted font size */
}

.music-player button {
    width: 30px; /* Set specific size for the button */
    height: 30px;
    padding: 0; /* Remove extra padding */
}
